import React, { useContext, useState, useEffect } from 'react';
import './PortfolioItem.component.scss';
import Layout from '../../components/Layout/Layout.component';
import { useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { AppContext } from '../../AppContext';
import CoverImage from '../../components/CoverImage/CoverImage.component.jsx';
import Wrapper from '../../components/Wrapper/Wrapper.component.jsx';
import Container from '../../components/Container/Container.component.jsx';  
import Markdown from 'markdown-to-jsx';
import SectionH2 from '../../components/SectionH2/Sectionh2.component';

export default function PortfolioItem({scrollYProgress}) {
  let location = useLocation();
  const pathname = location.pathname;
  const { portfolioData } = useContext(AppContext);
  const [textLink, setTextLink] = useState('');
  const [text, setText] = useState('');

  useEffect(() => {
    portfolioData.map( data => `/${data.link}` === pathname && setTextLink(data.markdown) );

    import(`../../data/markdown/${textLink}`)
      .then((res) => {
        fetch(res.default)
          .then((res) => res.text())
          .then((res) => setText(res))
          .catch((err) => console.log(err));
      })
      .catch((err) => console.log(err));

  }, [pathname, textLink, portfolioData]);

  return (
    <Layout>
      {
        portfolioData.map( data => (
          `/${data.link}` === pathname &&
          <>
            <Helmet>
              <title>{data.title}</title>
              <meta
                name="description"
                content={`ISR Technology - USA, MIAMI, FL. ${data.paragraph}`}
              />
            </Helmet>
            <CoverImage image={process.env.REACT_APP_PUBLIC_URL + data.image} title={data.title} scrollYProgress={scrollYProgress}/>
            <Wrapper>
              <Container 
                display={`flex`} 
                maxWidth={`768px`}
                classe={`services-container`}
              >
                <SectionH2 h2={data.title} />
                <div className="underline"></div>
                <p>
                  <Markdown>
                    {text}
                  </Markdown>
                </p>
              </Container>
            </Wrapper>
          </>
        )
      )}
    </Layout>
  );
}